.App {
  text-align: left;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

section#intro .wrap-content h1 {
  font-size: 144px;
  line-height: 170px;
  font-weight: 700;
  letter-spacing: 2px;
  position: relative;
}

@media screen and (max-width: 991px) {
  section#intro .wrap-content h1 {
    font-size: 121px;
    line-height: 160px;
  }
}

@media screen and (max-width: 600px) {
  section#intro .wrap-content h1 {
    font-size: 100px;
    line-height: 135px;
  }
}

@media screen and (max-width: 600px) {
  section#intro .wrap-content h1 {
    font-size: 81px;
    line-height: 115px;
  }
}

@media screen and (max-width: 430px) {
  section#intro .wrap-content h1 {
    font-size: 64px;
    line-height: 93px;
  }
}

@media screen and (max-width: 380px) {
  section#intro .wrap-content h1 {
    font-size: 49px;
    line-height: 80px;
  }
}

@media screen and (max-width: 767px) {
  section#intro .wrap-content h1.small {
    font-size: 110px;
    line-height: 125px;
  }
}

@media screen and (max-width: 600px) {
  section#intro .wrap-content h1.small {
    font-size: 80px;
    line-height: 95px;
  }
}

@media screen and (max-width: 480px) {
  section#intro .wrap-content h1.small {
    font-size: 48px;
    line-height: 60px;
  }
}

[class~="dot-highlight"] {
  color: #FF5252 !important;
}

h2,
h3 {
  font-size: 120px;
  font-weight: 700;
  margin-bottom: 50px;
  margin-left: -7px;
}

@media screen and (max-width: 1199px) {

  h2,
  h3 {
    font-size: 64px;
  }
}

@media screen and (max-width: 991px) {

  h2,
  h3 {
    font-size: 64px;
  }
}

@media screen and (max-width: 600px) {

  h2,
  h3 {
    margin-bottom: 25px;
  }
}

@media screen and (max-width: 480px) {

  h2,
  h3 {
    font-size: 50px;
    margin-left: -2px;
  }
}

@media screen and (max-width: 380px) {

  h2,
  h3 {
    font-size: 45px;
  }
}

#wrap-social {
  position: fixed;
  top: 250px;
  right: -200px;
  z-index: 99;
}

@media screen and (max-width: 991px) {
  #wrap-social {
    right: -225px;
    top: 235px;
  }
}

@media screen and (max-width: 767px) {
  #wrap-social {
    right: -255px;
  }
}

#wrap-social ul li {
  display: inline-block;
}

#wrap-social ul li a {
  color: white;
  font-family: "Source Code Pro", monospace;
  text-transform: uppercase;
  font-size: 18px;
  line-height: 28px;
  margin-right: 36px;
  text-decoration: none;
}

#wrap-social ul {
  -webkit-transform: rotate(-90deg);
  -ms-transform: rotate(-90deg);
  transform: rotate(-90deg);
}

footer .wrap-social ul li {
  display: inline-block;
}

footer .wrap-social ul li a {
  color: #282c34;
  text-transform: uppercase;
  font-family: "Source Code Pro";
  font-size: 18px;
  line-height: 28px;
  margin-right: 36px;
  text-decoration: none;
}

@media screen and (max-width: 480px) {
  footer h2 {
    font-size: 46px;
  }
}